<template>
  <h1 class="mb-4 text-2xl font-medium">Legal claim submission form</h1>

  <p>
    For officially released apps we have technologies that will prevent users from uploading unlicensed copies of your apps on appdb. We are also
    providing help in order to find and remove multiple variants of your apps, if they were uploaded previously.<br /><br />
    It is mandatory to include your licensing terms, as without license, we can not determine what exactly was violated.<br /><br />
    If your app is available for free, we are welcoming you to become
    <a
      :href="config.public.registerUrl"
      target="_blank"
    >
      an appdb Publisher
    </a>
    and implement our APIs to monetize your apps on appdb.<br /><br />
    If your app is paid, we are welcoming you to become
    <a
      :href="config.public.registerUrl"
      target="_blank"
    >
      an appdb Publisher
    </a>
    to sell your app on appdb.<br /><br />
    If you want us to contact you with help regarding your app publication on appdb, please leave a notice in the end of the claim and/or license.
  </p>

  <p class="my-7">Please fill this form in order to submit a claim:</p>

  <form class="grid grid-cols-4 gap-5 xl:grid-cols-2 md:grid-cols-1">
    <v-label label="Full name">
      <v-input v-model="form.name" />
    </v-label>

    <v-label label="Corporate email">
      <v-input
        v-model="form.email"
        type="email"
      />
    </v-label>

    <v-label label="Phone number in international format">
      <v-input
        v-model="form.phone"
        type="tel"
      />
    </v-label>

    <v-label label="URL where infringement content is located">
      <v-input
        v-model="form.url"
        type="url"
      />
    </v-label>

    <v-label
      label="License and claim texts"
      class="col-span-full"
    >
      <v-textarea
        v-model="form.license_text"
        :rows="3"
      />
    </v-label>

    <div class="col-span-full">
      <v-checkbox v-model="areTermsAccepted">
        By checking this box I agree with

        <span
          v-for="agreement of agreements"
          :key="agreement.id"
          class="not-last:after:content-[',_']"
        >
          <a
            :href="agreement.uri"
            target="_blank"
          >
            {{ agreement.name }}
          </a>
        </span>
      </v-checkbox>
    </div>

    <div class="col-span-full">
      <v-button
        :loading="isSubmitting"
        variant="primary"
        @click="submit"
      >
        Submit
      </v-button>
    </div>
  </form>
</template>

<script setup lang="ts">
import { clone } from "lodash-es"
import { load as loadRecaptcha } from "recaptcha-v3"

useHead({
  title: "Copyright Claim",
})

definePageMeta({
  requiredRole: "any",
  layout: "no-menu",
})

const config = useRuntimeConfig()

const isSubmitting = ref(false)
const emptyForm: Parameters<typeof submitCopyrightClaim>[0] = {
  url: "",
  name: "",
  phone: "",
  email: "",
  license_text: "",
  "g-recaptcha-response": "",
}
const form = ref(clone(emptyForm))
const areTermsAccepted = ref(false)

async function getRecaptchaToken() {
  const user = useUserStore()
  if (user.isAuthorized) {
    form.value["g-recaptcha-response"] = ""
    return
  }

  const config = useRuntimeConfig()
  const recaptcha = await loadRecaptcha(config.public.recaptchaKey, { autoHideBadge: true })
  const token = await recaptcha.execute("submit")
  form.value["g-recaptcha-response"] = token
}

async function submit() {
  if (!areTermsAccepted.value) {
    alert("You have to agree with terms and conditions")
    return
  }

  isSubmitting.value = true
  try {
    await getRecaptchaToken()
    await submitCopyrightClaim(form.value)
    alert("Your claim has been submitted. Please check your mailbox for further instructions")
    form.value = clone(emptyForm)
  } finally {
    isSubmitting.value = false
  }
}

const agreements = await getAgreements({ required_for: "copyright_claims" })
</script>
